import './picker.css';
import $ from '../util/dom';
import calendar from '../util/calendar';
import pickerTpl from './picker.html';
import groupTpl from './group.html';

class PickerItem {
	items = [];
	height = 238;
	itemHeight = 34;
	indicatorTop = 102;
	indicatorHeight = 34;
	onChange = $.noop;
	groupIndex = -1;
	defaultIndex = -1;
	$pickerItem = null;
	touching = false;
	ogY = 0;
	ogTranslate = 0;
	touchId = undefined;
	translate = 0;
	totalHeight = 0;
	animating = true;
	constructor(items, options) {
		this.items = items;
		if (options.onChange) this.onChange = options.onChange;
		this.defaultIndex = options.defaultIndex;
		this.groupIndex = options.groupIndex;
		this.$pickerItem = $($.render(groupTpl, {}));
		this.$pickerItem.on('touchstart', '.wnlui_picker__group', this.handleTouchStart.bind(this));
		this.$pickerItem.on('touchmove', '.wnlui_picker__group', this.handleTouchMove.bind(this));
		this.$pickerItem.on('touchend', '.wnlui_picker__group', this.handleTouchEnd.bind(this));
		if (this.items.length > 0) this.render();
	}
	render() {
		let groups = '';
		for (let index = 0; index < this.items.length; index += 1) {
			let item = this.items[index];
			groups += '<div v-for="item in items" class="wnlui_picker__item">' + item.label + '</div>';
		}
		this.$pickerItem.find('.wnlui_picker__content').html(groups);
		this.adjustPosition();
	}
	adjustPosition(defaultIndex) {
		if (defaultIndex && defaultIndex !== -1) {
			this.defaultIndex = defaultIndex;
		}
		else {
			defaultIndex = this.defaultIndex;
		}
		let items = this.items;
		let indicatorTop = this.indicatorTop;
		let itemHeight = this.itemHeight;
		const totalHeight = items.length * itemHeight;
		let translate = totalHeight <= indicatorTop ? indicatorTop : 0;
		if (defaultIndex > -1) {
			if (translate === 0) {
				let upperCount = Math.floor(indicatorTop / itemHeight);
				if (defaultIndex > upperCount) {
					//over
					let overCount = defaultIndex - upperCount;
					translate -= overCount * itemHeight;
				}
				else if (defaultIndex === upperCount) {
					translate = 0;
				}
				else {
					//less
					translate += (Math.abs(upperCount - defaultIndex) * itemHeight);
				}
			}
			else {
				//total item less than indicator height
				translate -= itemHeight * defaultIndex;
			}
		}
		this.ogTranslate = translate;
		this.totalHeight = totalHeight;
		this.translate = translate;
		setTimeout(() => {
			this.updateSelected(defaultIndex > -1);
		}, 0);
		this.setTransition(this.$pickerItem.find('.wnlui_picker__content'), 0.3);
		this.setTranslate(this.$pickerItem.find('.wnlui_picker__content'), this.translate);
	}
	handleTouchStart(e) {
		if (this.touching || this.items.length <= 1) return;
		this.touching = true;
		this.ogTranslate = this.translate;
		this.touchId = e.targetTouches[0].identifier;
		this.ogY = this.translate === 0 ? e.targetTouches[0].pageY : e.targetTouches[0].pageY - this.translate;
		this.animating = false;
		this.setTransition(this.$pickerItem.find('.wnlui_picker__content'), 0.3);
		e.stopPropagation();
		e.preventDefault();
	}
	handleTouchMove(e) {
		if (!this.touching || this.items.length <= 1) return;
		if (e.targetTouches[0].identifier !== this.touchId) return;
		const pageY = e.targetTouches[0].pageY;
		const diffY = pageY - this.ogY;
		this.translate = diffY;
		this.setTranslate(this.$pickerItem.find('.wnlui_picker__content'), this.translate);
		e.stopPropagation();
		e.preventDefault();
	}
	handleTouchEnd() {
		if (!this.touching || this.items.length <= 1) return;
		let indicatorTop = this.indicatorTop;
		let indicatorHeight = this.indicatorHeight;
		let itemHeight = this.itemHeight;
		let translate = this.translate;
		if (Math.abs(translate - this.ogTranslate) < (itemHeight * 0.51)) {
			translate = this.ogTranslate;
		}
		else if (translate > indicatorTop) {
			//top boundry
			translate = indicatorTop;
		}
		else if (translate + this.totalHeight < indicatorTop + indicatorHeight) {
			//bottom
			translate = (indicatorTop + indicatorHeight) - this.totalHeight;
		}
		else {
			//pass single item range but not exceed boundry
			let step = 0;
			let adjust = 0;
			let diff = (translate - this.ogTranslate) / itemHeight;
			if (Math.abs(diff) < 1) {
				step = diff > 0 ? 1 : -1;
			}
			else {
				adjust = Math.abs((diff % 1) * 100) > 50 ? 1 : 0;
				step = diff > 0 ? Math.floor(diff) + adjust : Math.ceil(diff) - adjust;
			}

			translate = this.ogTranslate + (step * itemHeight);
		}
		this.touching = false;
		this.ogY = 0;
		this.touchId = undefined;
		this.ogTranslate = 0;
		this.animating = true;
		this.translate = translate;
		setTimeout(() => {
			this.updateSelected();
		}, 0);
		this.setTransition(this.$pickerItem.find('.wnlui_picker__content'), 0.3);
		this.setTranslate(this.$pickerItem.find('.wnlui_picker__content'), this.translate);
	}
	setTransition($target, time) {
		if (this.animating) {
			return $target.css({
				'-webkit-transition': `all ${time}s`,
				transition: `all ${time}s`
			});
		}
		return $target.css({
			'-webkit-transition': 'none',
			transition: 'none'
		});
	}
	setTranslate($target, diff) {
		return $target.css({
			'-webkit-transform': `translate3d(0, ${diff}px, 0)`,
			transform: `translate3d(0, ${diff}px, 0)`
		});
	}
	updateSelected(propagate = true) {
		let selected = 0;
		for (let i = 0; i < this.items.length; i += 1) {
			let item = this.items[i];
			if (!item.disabled &&
				(this.translate + (this.itemHeight * i)) >= this.indicatorTop &&
				(this.translate + (this.itemHeight * i) + this.itemHeight) <= this.indicatorTop + this.indicatorHeight) {
				selected = i;
			}
		}
		this.selectIndex = selected;
		if (this.onChange && propagate) {
			this.onChange(this.items[selected], selected, this.groupIndex);
		}
	}
}
let lunarType = false; //false:  true:
let viewDate = new Date();
export default class DatePicker {
	minDate = new Date(1901, 1, 19); //
	maxDate = new Date(2099, 11, 31); //
	minLDate = [1901, 0, 1]; //    0 1
	maxLDate = [2099, 11, 20]; //
	defaultIndexList = [];
	dateNow = new Date();
	dateGroups = [];
	pickerItemGroups = [];
	$picker = null;
	defaults = {};
	isShow = true;
	isDisabled = true;
	constructor(options) {
		this.defaults = $.extend({
			className: '',
			container: 'body',
			onChange: $.noop,
			onCancel: $.noop,
			onConfirm: $.noop,
			showLunar: false,
			modal: true
		}, options);
		this.$picker = $($.render(pickerTpl, this.defaults));
		this.$picker.on('click', '.wnlui_mask', () => {
			this.hide();
			this.defaults.onCancel(this.defaultIndexList);
		}).on('click', '.wnlui_picker__action', () => {
			this.hide();
			this.defaults.onCancel(this.defaultIndexList);
		}).on('click', '#wnlui_picker-confirm', () => {
			let confirmObj = {
				dateObj: [],
				isSolar: !lunarType
			};
			if (confirmObj.isSolar) {
				let year = this.defaultIndexList[0] + this.minDate.getFullYear();
				let month = this.defaultIndexList[1];
				let day = this.defaultIndexList[2] + 1;
				confirmObj.dateObj = calendar.solar2lunar(year, month, day);
			}
			else {
				let year = this.defaultIndexList[0] + this.minLDate[0];
				let month = this.defaultIndexList[1];
				let day = this.defaultIndexList[2] + 1;
				let solarObj;
				if (calendar.leapMonth(year) > 0) {
					if (month < calendar.leapMonth(year)) {
						month += 1;
						solarObj = calendar.lunar2solar(year, month, day);
					}
					else if (month === calendar.leapMonth(year)) {
						solarObj = calendar.lunar2solar(year, month, day, true);
					}
					else {
						solarObj = calendar.lunar2solar(year, month, day);
					}
				}
				else {
					month += 1;
					solarObj = calendar.lunar2solar(year, month, day);
				}
				confirmObj.dateObj = solarObj;
			}
			viewDate = new Date(confirmObj.dateObj.cYear, confirmObj.dateObj.cMonth - 1, confirmObj.dateObj.cDay);
			this.defaults.onConfirm(confirmObj);
		}).on('click', '.date_l_btn', () => {
			this.dateLunarTap();
		});
		if (this.defaults.defaultValue) {
			viewDate = new Date(this.defaults.defaultValue[0], this.defaults.defaultValue[1] - 1, this.defaults.defaultValue[2]);
		}
		this.initSolarDateSelect();
		this.generatePickers();
	}
	disableScroll() {
		this.oldonwheel = window.onwheel;
		window.onwheel = this.preventDefault;
		this.oldonmousewheel = window.onmousewheel;
		window.onmousewheel = this.preventDefault;
		this.oldontouchmove = window.ontouchmove;
		window.ontouchmove = this.preventDefault;
		// document.body.addEventListener('touchmove',this.preventDefault);
		this.isDisabled = true;
	}
	preventDefault(e) {
		e = e || window.event;
		e.preventDefault();
		e.returnValue = false;
	}
	enableScroll() {
		if (!this.isDisabled) {
			return;
		}
		window.onwheel = this.oldonwheel;
		window.onmousewheel = this.oldonmousewheel;
		document.onkeydown = this.oldonkeydown;
		window.ontouchmove = this.oldontouchmove;
		// document.body.removeEventListener('touchmove',this.preventDefault);
		this.isDisabled = false;
	}
	dateLunarTap() {
		//
		if (lunarType) {
			this.$picker.find('.date_l_btn').removeClass('active');
			let isLeap = false;
			let year = this.defaultIndexList[0] + this.minLDate[0];
			let month = this.defaultIndexList[1];
			let day = this.defaultIndexList[2] + 1;
			if (calendar.leapMonth(year) > 0) {
				if (month < calendar.leapMonth(year)) {
					month += 1;
				}
				else if (month === calendar.leapMonth(year)) {
					isLeap = true;
				}
			}
			else {
				month += 1;
			}
			let solarObj = calendar.lunar2solar(year, month, day, isLeap);
			if (solarObj.cMonth - 1 < 0) {
				this.initSolarDateSelect(solarObj.cYear - 1, 11, solarObj.cDay);
			}
			else {
				this.initSolarDateSelect(solarObj.cYear, solarObj.cMonth - 1, solarObj.cDay);
			}
		}
		//
		else {
			this.$picker.find('.date_l_btn').addClass('active');
			let year = this.defaultIndexList[0] + this.minDate.getFullYear();
			let month = this.defaultIndexList[1];
			let day = this.defaultIndexList[2] + 1;
			this.initLunarDateSelect(year, month, day);
		}
		lunarType = !lunarType;
	}
	generatePickers() {
		for (let i = 0; i < this.dateGroups.length; i += 1) {
			let pickerItem = new PickerItem(this.dateGroups[i].items, {
				onChange: this.handleChange.bind(this),
				defaultIndex: this.defaultIndexList[i],
				groupIndex: i
			});
			this.pickerItemGroups.push(pickerItem);
			this.$picker.find('.wnlui_picker__bd').append(pickerItem.$pickerItem);
		}
	}
	show() {
		if (this.defaults.showLunar) {
			this.$picker.find('.date_select_footer').removeClass('hidden');
		}
		else {
			this.$picker.find('.date_select_footer').addClass('hidden');
		}
		lunarType = false;
		$(this.defaults.container).append(this.$picker);
		// . fix IOS10
		$.getStyle(this.$picker[0], 'transform');
		this.$picker.find('.wnlui_mask').addClass('wnlui_animate-fade-in');
		this.$picker.find('.wnlui_picker').addClass('wnlui_animate-slide-up');
		this.isShow = true;
		if (this.defaults.modal) {
			this.disableScroll();
		}
	}
	_hide(callback) {
		// this._hide = $.noop; // 
		this.$picker.find('.wnlui_mask').addClass('wnlui_animate-fade-out');
		this.$picker.find('.wnlui_picker').addClass('wnlui_animate-slide-down')
			.on('animationend webkitAnimationEnd', () => {
				if (!this.isShow && this.$picker && document.querySelector('.wnlui_mask')) {
					this.$picker.find('.wnlui_mask').removeClass('wnlui_animate-fade-out');
					this.$picker.find('.wnlui_picker').removeClass('wnlui_animate-slide-down');
					this.$picker.remove();
					if (callback) callback();
				}
			});
		this.isShow = false;
	}
	hide(callback) {
		this.enableScroll();
		this._hide(callback);
	}
	handleChange(item, i, groupIndex) {
		let lastIndex = this.defaultIndexList[groupIndex];
		if (lastIndex === i) {
			return;
		}
		this.defaultIndexList[groupIndex] = i;
		setTimeout(() => {
			this.onSelectChange(item, this.defaultIndexList[groupIndex], groupIndex, lastIndex);
			if (this.defaults.onChange) this.defaults.onChange(this.defaultIndexList);
		}, 0);
	}
	initSolarDateSelect(year, month, day) {
		year = year || viewDate.getFullYear();
		month = month || month === 0 ? month : viewDate.getMonth();
		day = day || viewDate.getDate();
		let i = 0;
		let yearList = {
			items: []
		};
		let yearToView = 0;
		let monthList = {
			items: []
		};
		let monthToView = 0;
		let dayList = {
			items: []
		};
		let dayToView = 0;
		for (i = this.minDate.getFullYear(); i <= this.maxDate.getFullYear(); i += 1) {
			if (year === i) {
				yearToView = i - this.minDate.getFullYear();
			}
			yearList.items.push({
				label: i + ''
			});
		}
		if (year < this.minDate.getFullYear()) {
			yearToView = 0;
		}
		if (year > this.maxDate.getFullYear()) {
			yearToView = this.maxDate.getFullYear() - this.minDate.getFullYear();
		}
		for (i = 0; i <= 11; i += 1) {
			if (month === i) {
				monthToView = i;
			}
			let valueString = (i + 1) + '';
			monthList.items.push({
				label: valueString
			});
		}
		let dayCount = calendar.solarDays(year, month);
		let dateObj = new Date(year, month, 1);
		for (i = 1; i <= dayCount; i += 1) {
			if (day === i) {
				dayToView = i - 1;
			}
			dateObj.setDate(i);
			if (year === this.dateNow.getFullYear() && month === this.dateNow.getMonth() && i === this.dateNow.getDate()) {
				dayList.items.push({
					label: ''
				});
			}
			else {
				dayList.items.push({
					label: i + '' + calendar.nStr1[dateObj.getDay()]
				});
			}
		}
		if (day > dayCount) {
			dayToView = dayCount - 1;
		}
		this.dateGroups[0] = yearList;
		this.dateGroups[1] = monthList;
		this.dateGroups[2] = dayList;
		this.defaultIndexList[0] = yearToView;
		this.defaultIndexList[1] = monthToView;
		this.defaultIndexList[2] = dayToView;
		this.renderPickerGroups();
		// this.$set(this.dateGroups, 0, yearList);
		// this.$set(this.defaultIndexList, 0, yearToView);
		// this.$set(this.dateGroups, 1, monthList);
		// this.$set(this.defaultIndexList, 1, monthToView);
		// this.$set(this.dateGroups, 2, dayList);
		// this.$set(this.defaultIndexList, 2, dayToView);
	}
	initLunarDateSelect(year, month, day) {
		let i = 0;
		let lunarObj = calendar.solar2lunar(year, month, day);
		let yearList = {
			items: []
		};
		let yearToView = 0;
		let monthList = {
			items: []
		};
		let monthToView = 0;
		let monthCount = 11;
		let dayList = {
			items: []
		};
		let dayToView = 0;
		let solarObj = {};
		for (i = this.minLDate[0]; i <= this.maxLDate[0]; i += 1) {
			if (lunarObj.lYear === i) {
				yearToView = i - this.minLDate[0];
			}
			yearList.items.push({
				label: i + ''
			});
		}
		let leapMonth = calendar.leapMonth(lunarObj.lYear);
		if (leapMonth > 0) {
			monthCount += 1;
		}
		for (i = 0; i <= monthCount; i += 1) {
			if (lunarObj.lMonth - 1 === i) {
				monthToView = i;
				if (leapMonth > 0 && lunarObj.lMonth > leapMonth) {
					monthToView += 1;
				}
			}
			if (leapMonth > 0) {
				if (i < leapMonth) {
					monthList.items.push({
						label: calendar.toChinaMonth(i + 1)
					});
				}
				else if (i === leapMonth) {
					monthList.items.push({
						label: '' + calendar.toChinaMonth(leapMonth)
					});
				}
				else {
					monthList.items.push({
						label: calendar.toChinaMonth(i)
					});
				}
			}
			else {
				monthList.items.push({
					label: calendar.toChinaMonth(i + 1)
				});
			}
		}
		if (lunarObj.isLeap) {
			monthToView = leapMonth;
		}
		let dayCount = (leapMonth === lunarObj.lMonth ? calendar.leapDays(lunarObj.lYear) : calendar.monthDays(lunarObj.lYear, lunarObj.lMonth));
		for (i = 1; i <= dayCount; i += 1) {
			if (lunarObj.lDay === i) {
				dayToView = i - 1;
			}
			solarObj = calendar.lunar2solar(lunarObj.lYear, lunarObj.lMonth, i, lunarObj.isLeap);
			if (solarObj.cYear === this.dateNow.getFullYear() &&
				solarObj.cMonth - 1 === this.dateNow.getMonth() && solarObj.cDay === this.dateNow.getDate()) {
				dayList.items.push({
					label: ''
				});
			}
			else {
				dayList.items.push({
					label: calendar.toChinaDay(i) + '' + calendar.nStr1[solarObj.nWeek]
				});
			}
		}
		this.dateGroups[0] = yearList;
		this.dateGroups[1] = monthList;
		this.dateGroups[2] = dayList;
		this.defaultIndexList[0] = yearToView;
		this.defaultIndexList[1] = monthToView;
		this.defaultIndexList[2] = dayToView;
		this.renderPickerGroups();
		// this.$set(this.dateGroups, 0, yearList);
		// this.$set(this.defaultIndexList, 0, yearToView);
		// this.$set(this.dateGroups, 1, monthList);
		// this.$set(this.defaultIndexList, 1, monthToView);
		// this.$set(this.dateGroups, 2, dayList);
		// this.$set(this.defaultIndexList, 2, dayToView);
	}
	renderPickerGroups() {
		if (!this.dateGroups || !this.pickerItemGroups || this.pickerItemGroups.length === 0) {
			return;
		}
		for (let i = 0; i < this.dateGroups.length; i += 1) {
			this.pickerItemGroups[i].items = this.dateGroups[i].items;
			this.pickerItemGroups[i].render();
			this.pickerItemGroups[i].adjustPosition(this.defaultIndexList[i]);
		}
	}
	onSelectChange(item, selectIndex, groupIndex, lastIndex) {
		if (groupIndex === 0 && selectIndex < this.dateGroups[0].items.length) {
			this.defaultIndexList[0] = selectIndex;
			this.getDateSelectMonthInfo(lastIndex);
			this.getDateSelectDayInfo();
		}
		else if (groupIndex === 1 && this.defaultIndexList[1] < this.dateGroups[1].items.length) {
			//
			if (!lunarType) {
				if (this.defaultIndexList[0] === 0 && selectIndex < this.minDate.getMonth()) {
					this.defaultIndexList[1] = this.minDate.getMonth();
					this.pickerItemGroups[groupIndex].adjustPosition(this.defaultIndexList[1]);
					// this.$refs['pickerItem' + groupIndex][0].$emit('adjustPosition', this.defaultIndexList[1]);
				}
				else {
					this.defaultIndexList[1] = selectIndex;
				}
				this.getDateSelectDayInfo();
				return;
			}
			//
			if (this.defaultIndexList[0] === 0 && selectIndex > this.maxLDate[1]) {
				this.defaultIndexList[1] = this.maxLDate[1];
				this.pickerItemGroups[groupIndex].adjustPosition(this.defaultIndexList[1]);
				// this.$refs['pickerItem' + groupIndex][0].$emit('adjustPosition', this.defaultIndexList[1]);
			}
			else {
				this.defaultIndexList[1] = selectIndex;
			}
			this.getDateSelectDayInfo();
		}
		else if (groupIndex === 2 && this.defaultIndexList[2] + 1 < this.dateGroups[2].items.length) {
			//
			if (!lunarType) {
				if (this.defaultIndexList[0] === 0 && this.defaultIndexList[1] === this.minDate.getMonth() && selectIndex < this.minDate.getDate()) {
					this.defaultIndexList[2] = this.minDate.getDate() - 1;
					this.pickerItemGroups[groupIndex].adjustPosition(this.defaultIndexList[2]);
					// this.$refs['pickerItem' + groupIndex][0].$emit('adjustPosition', this.defaultIndexList[2]);
				}
				else {
					this.defaultIndexList[2] = selectIndex;
				}
				return;
			}
			//
			if (this.defaultIndexList[0] === this.maxLDate[0] - this.minLDate[0] &&
				this.defaultIndexList[1] === this.maxLDate[1] && selectIndex + 1 > this.maxLDate[2]) {
				this.defaultIndexList[2] = this.maxLDate[2] - 1;
				this.pickerItemGroups[groupIndex].adjustPosition(this.defaultIndexList[2]);
				// this.$refs['pickerItem' + groupIndex][0].$emit('adjustPosition', this.defaultIndexList[2]);
			}
			else {
				this.defaultIndexList[2] = selectIndex;
			}
		}
	}
	getDateSelectMonthInfo(lastIndex) {
		let monthList = {
			items: []
		};
		let monthToView = 0;
		let year = viewDate.getFullYear();
		let month = viewDate.getMonth();
		let i = 0;
		//
		if (!lunarType) {
			year = this.defaultIndexList[0] + this.minDate.getFullYear();
			month = this.defaultIndexList[1];
			for (i = 0; i <= 11; i += 1) {
				if (month === i) {
					monthToView = i;
				}
				let valueString = (i + 1) + '';
				monthList.items.push({
					index: valueString,
					label: valueString
				});
			}
		}
		//
		else {
			year = this.defaultIndexList[0] + this.minLDate[0];
			month = this.defaultIndexList[1];
			let leapMonth = calendar.leapMonth(year);
			let monthCount = 11;
			if (leapMonth > 0) {
				monthCount += 1;
			}
			for (i = 0; i <= monthCount; i += 1) {
				if (month === i) {
					monthToView = i;
					//
					if (leapMonth > 0) {
						if (month >= leapMonth) {
							monthToView += 1;
						}
					}
					//
					else {
						//
						let prevLeapMonth = lastIndex ? calendar.leapMonth(lastIndex + +this.minLDate[0]) : 0;
						if (prevLeapMonth > 0 && month >= prevLeapMonth) {
							monthToView -= 1;
						}
					}
				}
				if (leapMonth > 0) {
					if (i < leapMonth) {
						monthList.items.push({
							index: i,
							label: calendar.toChinaMonth(i + 1)
						});
					}
					else if (i === leapMonth) {
						monthList.items.push({
							index: i,
							label: '' + calendar.toChinaMonth(leapMonth)
						});
					}
					else {
						monthList.items.push({
							index: i,
							label: calendar.toChinaMonth(i)
						});
					}
				}
				else {
					monthList.items.push({
						index: i,
						label: calendar.toChinaMonth(i + 1)
					});
				}
			}
		}
		this.dateGroups[1] = monthList;
		this.defaultIndexList[1] = monthToView;
		this.pickerItemGroups[1].items = monthList.items;
		// this.pickerItemGroups[1].defaultIndex = monthToView;
		this.pickerItemGroups[1].render();
		this.pickerItemGroups[1].adjustPosition(this.defaultIndexList[1]);
		// this.$set(this.dateGroups, 1, monthList);
		// this.$set(this.defaultIndexList, 1, monthToView);
	}
	getDateSelectDayInfo() {
		let dayList = {
			items: []
		};
		let dayToView = 0;
		let i = 0;
		//
		if (!lunarType) {
			let year = this.defaultIndexList[0] + this.minDate.getFullYear();
			let month = this.defaultIndexList[1];
			let day = this.defaultIndexList[2] + 1;
			let dayCount = calendar.solarDays(year, month);
			let dateObj = new Date(year, month, 1);
			for (i = 1; i <= dayCount; i += 1) {
				if (day === i) {
					dayToView = i - 1;
				}
				dateObj.setDate(i);
				if (year === this.dateNow.getFullYear() && month === this.dateNow.getMonth() && i === this.dateNow.getDate()) {
					dayList.items.push({
						index: i,
						label: ''
					});
				}
				else {
					dayList.items.push({
						index: i,
						label: i + '' + calendar.nStr1[dateObj.getDay()]
					});
				}
			}
			if (day > dayCount) {
				dayToView = dayCount - 1;
			}
		}
		//
		else {
			let isLeap = false;
			let year = this.defaultIndexList[0] + this.minLDate[0];
			let month = this.defaultIndexList[1];
			let day = this.defaultIndexList[2] + 1;
			if (calendar.leapMonth(year) > 0) {
				if (month < calendar.leapMonth(year)) {
					month += 1;
				}
				else if (month === calendar.leapMonth(year)) {
					isLeap = true;
				}
			}
			else {
				month += 1;
			}
			let dayCount = isLeap ? calendar.leapDays(year) : calendar.monthDays(year, month);
			let solarObj = {};
			for (i = 1; i <= dayCount; i += 1) {
				if (day === i) {
					dayToView = i - 1;
				}
				solarObj = calendar.lunar2solar(year, month, i, isLeap);
				dayList.items.push({
					index: i,
					label: calendar.toChinaDay(i) + '' + calendar.nStr1[solarObj.nWeek]
				});
			}
		}
		this.dateGroups[2] = dayList;
		this.defaultIndexList[2] = dayToView;
		this.pickerItemGroups[2].items = dayList.items;
		// this.pickerItemGroups[2].defaultIndex = dayToView;
		this.pickerItemGroups[2].render();
		this.pickerItemGroups[2].adjustPosition(this.defaultIndexList[2]);
		// this.$set(this.dateGroups, 2, dayList);
		// this.$set(this.defaultIndexList, 2, dayToView);
	}
}



