/*eslint-disable*/
import './picker.css';
import $ from '../util/dom';
import cron from './cron';
import './scroll';
import pickerTpl from './picker.html';
import groupTpl from './group.html';

function Result(item) {
	this.label = item.label;
	this.value = item.value;
	if (item.longitude) {
		this.longitude = item.longitude;
	}
	if (item.latitude) {
		this.latitude = item.latitude;
	}
}
Result.prototype.toString = function() {
	return this.value;
};
Result.prototype.valueOf = function() {
	return this.value;
};

let _sington;
let temp = {}; // temp 

/**
 * picker 
 * @param {array} items pickerpickerpickerexample
 * @param {Object} options 
 * @param {number=} [options.depth] picker(picker) 1-3items
 * @param {string=} [options.id=default] pickeriddefaultValueid
 * @param {string=} [options.className] 
 * @param {string=} [options.container] 
 * @param {array=} [options.defaultValue] value
 * @param {function=} [options.onChange] picker
 * @param {function=} [options.onConfirm] ""(Array)picker
 */
function picker() {
	if (_sington) return _sington;
	// 
	const options = arguments[arguments.length - 1];
	const defaults = $.extend({
		id: 'default',
		className: '',
		container: 'body',
		onChange: $.noop,
		onConfirm: $.noop
	}, options);

	// 
	let items;
	let isMulti = false; // 
	if (arguments.length > 2) {
		let i = 0;
		items = [];
		while (i < arguments.length - 1) {
			items.push(arguments[i++]);
		}
		isMulti = true;
	}
	else {
		items = arguments[0];
	}
	const depthOf = (object) => {
		let depth = 1;
		if (object.children && object.children[0]) {
			depth = depthOf(object.children[0]) + 1;
		}
		return depth;
	};
	// 
	temp[defaults.id] = temp[defaults.id] || [];
	const result = [];
	const lineTemp = temp[defaults.id];
	const $picker = $($.render(pickerTpl, defaults));
	let depth = options.depth || (isMulti ? items.length : depthOf(items[0])),
		groups = '';
	// 
	function show() {
		$(defaults.container).append($picker);
		// . fix IOS10
		$.getStyle($picker[0], 'transform');
		$picker.find('.wnlui_mask').addClass('wnlui_animate-fade-in');
		$picker.find('.wnlui_picker').addClass('wnlui_animate-slide-up');
	}

	function _hide(callback) {
		_hide = $.noop; // 
		$picker.find('.wnlui_mask').addClass('wnlui_animate-fade-out');
		$picker.find('.wnlui_picker')
			.addClass('wnlui_animate-slide-down')
			.on('animationend webkitAnimationEnd', function() {
				$picker.remove();
				_sington = false;
				callback && callback();
			});
	}

	function hide(callback) {
		_hide(callback);
	}
	// 
	function scroll(items, level) {
		if (lineTemp[level] === undefined && defaults.defaultValue && defaults.defaultValue[level] !== undefined) {
			// defaultValue
			const defaultVal = defaults.defaultValue[level];
			let index = 0,
				len = items.length;

			for (; index < len; ++index) {
				if (defaultVal == items[index].value) break;
			}
			if (index < len) {
				lineTemp[level] = index;
			}
			else {
				console.warn('Picker has not match defaultValue: ' + defaultVal);
			}
		}
		$picker.find('.wnlui_picker__group').eq(level).scroll({
			items: items,
			temp: lineTemp[level],
			onChange: function(item, index) {
				//result
				if (item) {
					result[level] = new Result(item);
				}
				else {
					result[level] = null;
				}
				lineTemp[level] = index;
				if (isMulti) {
					defaults.onChange(result);
				}
				else {
					/**
					 * @
					 * 1. 0
					 * 2. 
					 *
					 * @
					 * 1. callonChange
					 * 2. onChangecall
					 */
					if (item.children && item.children.length > 0) {
						$picker.find('.wnlui_picker__group').eq(level + 1).show();
						!isMulti && scroll(item.children, level + 1); // children
					}
					else {
						//test
						const $items = $picker.find('.wnlui_picker__group');
						$items.forEach((ele, index) => {
							if (index > level) {
								$(ele).hide();
							}
						});
						result.splice(level + 1);
						defaults.onChange(result);
					}
				}
			},
			onConfirm: defaults.onConfirm
		});
	}
	while (depth--) {
		groups += groupTpl;
	}
	$picker.find('.wnlui_picker__bd').html(groups);
	show();
	if (isMulti) {
		items.forEach((item, index) => {
			scroll(item, index);
		});
	}
	else {
		scroll(items, 0);
	}
	if (defaults.showLunar) {
		$picker.find('.date_select_footer').removeClass('hidden');
	}
	else {
		$picker.find('.date_select_footer').addClass('hidden');
	}
	$picker
		.on('click', '.wnlui_mask', function() {
			hide();
		})
		.on('click', '.wnlui_picker__action', function() {
			hide();
		})
		.on('click', '#wnlui_picker-confirm', function() {
			defaults.onConfirm(result);
		});
	_sington = $picker[0];
	_sington.hide = hide;
	return _sington;
}
export default {
	picker
};



