/* eslint-disable global-require */
import $ from '../util/dom';
import pickerObj from './picker';

export default class CityPicker {
	defaults = {};
	cityData = [];
	constructor(options) {
		this.defaults = $.extend({
			id: 'cityPicker',
			className: '',
			container: 'body',
			onChange: $.noop,
			onConfirm: $.noop,
			defaultValue: ['01', '0101', '101010100'],
			cityCode: true,
			showAuto: false,
			level: 3
		}, options);
		if (this.defaults.cityCode) { //citycodevaluecode
			require.ensure(['./province.json', './city.json', './county.json'], () => {
				let provinceList = require('./province.json');
				let cityList = require('./city.json');
				let countyList = require('./county.json');
				provinceList.forEach((element, index) => {
					if (!this.defaults.showAuto && index === 0) {
						return;
					}
					this.cityData.push({
						label: element.name,
						value: element.id
					});
				});
				if (this.defaults.level > 1) {
					this.cityData.forEach((element) => {
						element.children = this.getCityList(cityList, element.value);
					});
				}
				if (this.defaults.level > 2) {
					this.cityData.forEach((element) => {
						element.children.forEach((element1) => {
							element1.children = this.getCountyList(countyList, element1.value);
						});
					});
				}
			});
		}
		else { //valuelabel
			this.defaults.defaultValue = ['', '', '']; //value
			require.ensure(['./cityData.json'], () => {
				let cityData = require('../assets/citydata/cityData.json');
				cityData.forEach((element) => {
					element.value = element.label;
					if (this.defaults.level > 1) {
						element.children.forEach((element1) => {
							element1.value = element1.label;
							if (this.defaults.level > 2) {
								element1.children.forEach((element2) => {
									element2.value = element2.label;
								});
							}
							else {
								delete element1.children;
							}
						});
					}
					else {
						delete element.children;
					}
				});
				if (!this.defaults.showAuto) {
					cityData.splice(0, 1);
				}
				this.cityData = cityData;
			});
		}
	}
	getCityList(cityList, provinceId) {
		let localList = cityList[provinceId];
		let resultList = [];
		if (!localList || localList.length === 0) {
			return resultList;
		}
		localList.forEach((element) => {
			resultList.push({
				label: element.name,
				value: element.id
			});
		});
		return resultList;
	}
	getCountyList(countyList, cityId) {
		let localList = countyList[cityId];
		let resultList = [];
		if (!localList || localList.length === 0) {
			return resultList;
		}
		localList.forEach((element) => {
			resultList.push({
				label: element.name,
				value: element.id
			});
		});
		return resultList;
	}
	show() {
		pickerObj.picker(this.cityData, this.defaults);
	}
}



